        <list class="list list-ios getchips-list" v-for="(data, index) in chipsData" :key="index" v-if="showItem(data.offNum)">
          <item class="list-item hl-item-borderless" thin>
            <div class="list-item-inner">
              <div class="list-image-container">
                <img :src="data.iconOn" alt="" class="list-image">
              </div>
              <div class="right-container">
                <div class="info-box" v-for="(title, infoIndex) in data.title" :key="infoIndex">
                  <div class="hairline-stand-alone half"></div>
                  <div class="num">
                    <div class="amount-and-plus">
                      <span class="amount" v-text="data.chips[infoIndex]"></span>
                      <span class="plus">+</span>
                    </div>
                    <div class="chip">
                      <span>筹码</span>
                    </div>
                  </div>
                  <div class="vertical-separator">
                    <div class="hairline-stand-alone vertical"></div>
                  </div>
                  <div class="main-content">
                    <div><span class="title" v-text="title"></span></div>
                    <div><span class="sub-title" :class="{assertive: failed}" v-text="data.subtitle"></span></div>
                  </div>
                  <div class="side-content">
                    <button  @click='goTo(data.linkTo)' class="go-btn" type="button" name="button">前往</button>
                  </div>
                </div>
                <div class="hairline-stand-alone shift-right"></div>
                <div class="list-item-footer">
                  <span class="exclamation-mark">!</span> <span v-text="data.footerText"></span>
                </div>
              </div>
            </div>
          </item>
        </list>

        <list class="list list-ios getchips-list done-list" v-for="item in completed" :key="item + 1">
          <item class="list-item hl-item-borderless" thin>
            <div class="list-item-inner">
              <div class="list-image-container">
                <img :src="getOffItem(item).iconOff" alt="" class="list-image">
              </div>
              <div class="right-container">
                <div class="info-box" v-for="(title, OffInfoIndex) in getOffItem(item).title" :key="OffInfoIndex">
                  <div class="hairline-stand-alone half"></div>
                  <div class="num">
                    <div class="amount-and-plus">
                      <span class="amount" v-text="getOffItem(item).chips[OffInfoIndex]"></span>
                      <span class="plus">+</span>
                    </div>
                    <div class="chip">
                      <span>筹码</span>
                    </div>
                  </div>
                  <div class="vertical-separator">
                    <div class="hairline-stand-alone vertical"></div>
                  </div>
                  <div class="main-content">
                    <div><span class="title" v-text="title"></span></div>
                    <div><span class="sub-title" :class="{assertive: failed}" v-text="getOffItem(item).subtitle"></span></div>
                  </div>
                  <div class="side-content">
                    <div class="stamp-image"></div>
                  </div>
                </div>
                <div class="hairline-stand-alone shift-right"></div>
                <div class="list-item-footer">
                  <span class="exclamation-mark">!</span> <span v-text="getOffItem(item).footerText"></span>
                </div>
              </div>
            </div>
          </item>
        </list>
        <div class="divider"></div>
      </div>
    </div>
  </div>
</template>
