            margin-top: 6px;

            @include font-regular();
          }
        }
      }
    }

    .get-chips-list {
      .list {
        margin-bottom: 10px;
      }

      .list-item {
        padding: 0;
        position: relative;

        .list-item-inner {
          padding: 16px 0 0;
          overflow: hidden;
          padding-right: 16px;
        }

