                color: #AAAAAA;
              }

              .amount-and-plus {
                overflow: hidden;
                margin-top: 2px;
                color: #EA5A49;
              }

              .plus, .amount {
                @include font-regular();

                display: block;
                float: right !important;
              }

              .plus {
                font-size: 11px;
                line-height: 14px;
                width: 11px;
                text-align: center;
                margin-top: 4px;
                margin-right: 2px;
              }

              .amount {
