                background: url(//qianka.b0.upaiyun.com/images/03b41c338d30136fd6d872e5c4157c19.png) center center no-repeat;
                width: 65px;
                height: 65px;
                background-size: 100% 100%;
              }
            }

            .hairline-stand-alone.half {
              width: 50%;
              margin: 10px 0;
            }

            &:first-child {
              .hairline-stand-alone.half {
                display: none;
