  }
</style>
<script>
  const HIGHEARN_OFF_NUM = 1
  const HIDE_WITHDRAW_OFF_NUM = 7
  const TASKS_OFF_NUM = 8
  const CREDIT_CARDS_OFF_NUM = 9
  const WITHDRAW_ITEMS = [4, 5, 6] // 提现
  export default {
    data () {
      return {
        today_chips: 0,
        available_chips: 0,
        is_old: 1, // 是否老用户 1是 0否
        create_day: 20170731, // 用户创建日期
        hide_withdraw_flag: false,
        completed: [],
        chipsData: [
          { // 试玩任务
            iconOn: '//qianka.b0.upaiyun.com/images/c2a93bb6f129bcb21eaa6d155968477c.png',
            iconOff: '//qianka.b0.upaiyun.com/images/b8ea33bbc850b39051b0184b5fc0b8e5.png',
            title: ['完成4个试玩任务'],
            offNum: TASKS_OFF_NUM,
            chips: ['1'],
            subtitle: '新用户专享',
            linkTo: '/tasks',
            footerText: '4个试玩任务均需在加入钱咖的当日完成'
          },

          { // 信用卡
            iconOn: '//qianka.b0.upaiyun.com/images/b779bb6bdd3e6e4583752ec8831ee34b.png',
            iconOff: '//qianka.b0.upaiyun.com/images/b779bb6bdd3e6e4583752ec8831ee34b.png',
            title: ['完成一个办卡任务'],
            offNum: CREDIT_CARDS_OFF_NUM,
            chips: ['1'],
            subtitle: '单日可申请多种卡片',
            linkTo: '/credit/list',
            footerText: '奖励将在比对完成后发放'
          },

          { // 高额
            iconOn: '//qianka.b0.upaiyun.com/images/2cea4c22fca5e33886046fe6ddadf4c4.png',
            iconOff: '//qianka.b0.upaiyun.com/images/da07e79bb2aeb78229630fbcecb83222.png',
            title: ['完成一个注册任务'],
            offNum: HIGHEARN_OFF_NUM,
            chips: ['1'],
            subtitle: '单日可重复',
            linkTo: '/gaoe',
            footerText: '奖励将在比对完成或申诉成功后发放'
          },

          { // 收徒1
            iconOn: '//qianka.b0.upaiyun.com/images/de0f89d8908e4e73026cdf418fd66e75.png',
            iconOff: '//qianka.b0.upaiyun.com/images/92db0d3c22fe734ebaa1f028ff5a448e.png',
            title: ['当日收徒1人'],
            offNum: 2,
            chips: ['1'],
            subtitle: '每日0点重置',
            linkTo: '/apprentice',
            footerText: '每个iOS徒弟需在加入钱咖当日完成2个试玩任务'
          },

          { // 收徒3
            iconOn: '//qianka.b0.upaiyun.com/images/766e4dec25b72904f3aabb841bf564e7.png',
            iconOff: '//qianka.b0.upaiyun.com/images/afa6bfa4fe97d568d3f01906b247611b.png',
            title: ['当日收徒3人'],
            offNum: 3,
            chips: ['3'],
            subtitle: '每日0点重置',
            linkTo: '/apprentice',
            footerText: '每个iOS徒弟需在加入钱咖当日完成2个试玩任务'
          },

          { // 提现
            iconOn: '//qianka.b0.upaiyun.com/images/7002381c931ce2383ec64d0f70681c18.png',
            iconOff: '//qianka.b0.upaiyun.com/images/ef4116675dd7fe808723912cb3d9cb3e.png',
            title: ['提现 30元', '提现 50元', '提现 100元'],
            offNum: WITHDRAW_ITEMS[0],
            chips: ['1', '2', '5'],
            subtitle: '每日审核通过的首笔提现',
            linkTo: '/withdraw',
            footerText: '奖励将在提现审核通过后发放'
          }
        ]
      }
    },

    mounted () {
      $api.lotteryGetChips()
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          this.today_chips = payload.today
          this.is_old = payload.is_old
          this.create_day = payload.create_day
          this.available_chips = payload.available_chips

          // 先关闭，过段时间可能再开启测试
          // let create_day = parseInt(this.create_day)
          // if (create_day >= 20170728 && create_day <= 20170802) {
          //   this.chipsData.splice(1, 2)
          // }
          this.completed = this.processArray(payload.completed_obtain)
        })
    },

    methods: {
      back () {
        let path = _.get(this.$route.query, 'backroute', '/lottery')
        $router.back({ path })
      },

      processArray (arg) {
        _.pull(arg, HIGHEARN_OFF_NUM) // HIGHEARN_OFF_NUM has no completed state
        _.pull(arg, CREDIT_CARDS_OFF_NUM) // CREDIT_CARDS_OFF_NUM has no completed state
        if (this.is_old) {
          _.pull(arg, TASKS_OFF_NUM) // CREDIT_CARDS_OFF_NUM has no completed state
        }

        let result = _.filter(arg, function (o) { return (o < WITHDRAW_ITEMS[0]) }) // take everything below the withdraw index
        let minWithdrawIndex = []

        if ((arg.indexOf(HIDE_WITHDRAW_OFF_NUM) > -1)) { // don't show withdraw if HIDE_WITHDRAW_OFF_NUM exist
          this.hide_withdraw_flag = true
        } else {
          if ((arg.indexOf(WITHDRAW_ITEMS[0]) > -1)) {
            minWithdrawIndex.push(arg.indexOf(WITHDRAW_ITEMS[0]))
          } else if ((arg.indexOf(WITHDRAW_ITEMS[1]) > -1)) {
            minWithdrawIndex.push(arg.indexOf(WITHDRAW_ITEMS[1]))
          } else if ((arg.indexOf(WITHDRAW_ITEMS[2]) > -1)) {
            minWithdrawIndex.push(arg.indexOf(WITHDRAW_ITEMS[2]))
          }

          if (minWithdrawIndex.length) {
            result.splice(_.min(minWithdrawIndex), 0, WITHDRAW_ITEMS[0])
          }
        }

        if ((arg.indexOf(TASKS_OFF_NUM) > -1)) {
          result.push(TASKS_OFF_NUM)
        }

        return result
      },

      goToDetail () {
        $router.forward('/lottery/chipsDetail/canUse')
      },

      showItem (offNum) {
        if ((offNum === WITHDRAW_ITEMS[0]) && this.hide_withdraw_flag) {
          return false
        }

        if (this.is_old && (offNum === TASKS_OFF_NUM)) {
          return false
        }

        return this.completed.indexOf(offNum) <= -1
      },

      getOffItem (offNum) {
        return _.find(this.chipsData, {offNum: offNum})
      },

      goTo (path) {
        $router.forward({
          path: path,
          query: { backroute: '/lottery/getChips' }
        })
      }
    }
  }
</script>



